}






.nav_links {
  display: none;
  position: fixed;
  top: 60px;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10;
  text-align: left;
  padding: 24px;
  background-color: var(--white);
}

.nav_links ul {
  list-style: none;
  color: gray;
  padding: 0;
}

.nav_links li {
  padding-bottom: 24px;
}

.nav_links li:last-child {
  padding-bottom: 0px;
}

.nav_links li>a {
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 22px;
  color: var(--grey-500);
  text-decoration: none;
}

.nav_links li>a:hover,
.nav_links li>a:focus {
  color: var(--green-600);
}

